<template>
    <div id="intelligence">
        <b-alert :show="error.length>0" variant="warning">{{error}}</b-alert>
        <div class="date_select row">
            <div class="col">
                <div class="add_resource">
                    <treeselect placeholder="select category" v-model="category_id" :multiple="true"
                                :options="categoriesOptions.children"
                    />
                    <!--                    :loadOptions="loadOptions"-->
                </div>
            </div>
            <div class="col">
                <div class="add_resource">
                    <treeselect placeholder="select tag" v-model="model.tag" :multiple="true"
                                :options="tags"
                    />
                </div>
            </div>
            <datepicker :format="'yyyy-MM-dd'" v-model="date">
            </datepicker>
            <div style="margin-left: 10px">
                <b-btn @click="refreshResources">show</b-btn>
            </div>
            <b-btn v-b-modal.modal-1 style="margin-right: 20px; margin-left: 20px"
                   @click="delete intelligence_model['id']">
                <i class="fa fa-plus"></i>
            </b-btn>
            <b-modal id="modal-1"
                     :header-bg-variant="headerBgVariant"
                     :header-text-variant="headerTextVariant"
                     :body-bg-variant="bodyBgVariant"
                     :body-text-variant="bodyTextVariant"
                     :footer-bg-variant="footerBgVariant"
                     :footer-text-variant="footerTextVariant"
                     @ok="handleOk"
            >
                <template v-slot:modal-title>
                    {{intelligence_model.id ? 'Edit Intelligence ID#' + intelligence_model.id : 'New Intelligence'}}
                </template>
                <b-form-group class="add_intelligence">
                    <!--both-->
                    <b-input-group prepend="url/guid" class="mt-3">
