                    </b-input-group>

                    <!--both-->
                    <b-input-group prepend="title" class="mt-3">
                        <b-input v-model="intelligence_model.item.title"></b-input>
                    </b-input-group>

                    <!--both-->
                    <b-input-group prepend="author" class="mt-3">
                        <b-input v-model="intelligence_model.item.author.name"></b-input>
                    </b-input-group>

                    <!--both-->
                    <b-input-group prepend="description" class="mt-3">
                        <b-input v-model="intelligence_model.item.description"></b-input>
                    </b-input-group>

<!--                    only for add, and manually add-->
                    <treeselect v-if="intelligence_model.hasOwnProperty('id')===false || intelligence_model.source_id === 0"
                                placeholder="select category" v-model="intelligence_model.category_id"
