                                style="margin-top: 15px;"
                    />

                    <!--                    :loadOptions="loadOptions"-->

                    <!--both-->
                    <b-form-tags style="margin-top: 15px;" v-model="intelligence_model.tags" class="mb-2"></b-form-tags>
                </b-form-group>
