                            <p>
                                <span v-if="props.data.item.hasOwnProperty('author')" class="fas fa-feather-alt"
                                      style="padding-right: 10px;"> {{props.data.item.author.name}} </span>
                                <a target="_blank" :href="props.data.item.link" class="fas fa-external-link-alt">
