                                <i class="fas fa-coins"> {{props.data.category_name}} </i>
                            </b-btn>
                            <i class="fab fa-chrome" style="padding-left:10px; padding-right:5px;"></i><span>{{come_from(props.data.guid)}}</span>
                            <br>
                            <i class="fas fa-hashtag"></i>
                            <span>{{props.data.item.tags}}</span>
                            <a href="#" style="margin-left: 3px;"> #tag1 </a>
                            <a href="#" style="margin-left: 3px;"> #tag2 </a>
                            <br>
                            <div>
                                <hr style="border-top: 1px solid rgba(255, 255, 255, 0.4)"
                                    v-show="props.data.comment.length>0">
                                <span style="font-size: 30px;" v-show="props.data.comment.length > 0">“</span>
                                <span :ref="'comment-span-'+props.data.id"> {{props.data.comment}} </span>
                                <b-textarea :ref="'comment-textarea-'+props.data.id" v-model="comment"
                                            v-show="false">
                                </b-textarea>
                                <span style="font-size: 30px;" v-show="props.data.comment.length>0">”</span>
                            </div>
                            <span style="float: right">
                                <b-btn @click="click_todo(props)">
                                    <i class="fas fa-clipboard-list"
                                       :style="props.data.todo===1 ? 'color: #82c91e;' : ''"> todo </i>
                                </b-btn>
                                <b-btn :ref="'comment-button-'+props.data.id"
                                       @click="click_comment(props.data.id, props)" style="margin-left: 10px;">
                                    <i class="fas fa-comment"> comment </i>
                                </b-btn>
                                <b-btn v-b-modal.modal-1 style="margin-left: 10px;"
                                       @click="populateResourceToEdit(props.data)">
                                    <i class="fas fa-pen"> edit </i>
                                </b-btn>
                                <b-btn style="margin-left: 10px;">
