<script>
    import api from "@/api";
    import Waterfall from "vue-waterfall-plugin";
    Waterfall.computed.itemWidth = function () {
        const {screenWidth, gutter, width, phoneCol} = this;
        if (screenWidth >= 1200) {
            return width;
        } else {
            // return (screenWidth - (phoneCol + 1) * gutter) / phoneCol;
            return (screenWidth - (phoneCol + 1) * gutter) / phoneCol - 90;
        }
    };
    import Datepicker from 'vuejs-datepicker';
    // import the component
    import Treeselect from '@riophae/vue-treeselect'
    // import the styles
    import '@riophae/vue-treeselect/dist/vue-treeselect.css'

    export default {
        name: 'Intelligence',
        components: {
            Waterfall,
            Datepicker,
            // eslint-disable-next-line vue/no-unused-components
            Treeselect,
        },
        data() {
            return {
                items: [],
                resources: [],
                error: "",
                // for date filter
                date: null,
                // for category filter
                category_id: [],
                // for limit filter
                limit: 100,
                model: [],
                categories: [{'value': 1, 'text': 'ca'}],
                categoriesOptions: [],
                key: 0,
                value: ['apple', 'orange'],
                intelligence_model: {
                    item: {
                        author: {
                            name: "",
                        },
                    }
                },
                variants: ['primary', 'secondary', 'success', 'warning', 'danger', 'info', 'light', 'dark'],
                headerBgVariant: 'dark',
                headerTextVariant: 'light',
                bodyBgVariant: 'dark',
                bodyTextVariant: 'dark',
                footerBgVariant: 'dark',
                footerTextVariant: 'dark',
                tags: [
                    {"id": 1, "name": "linux", "label": "linux"},
                    {"id": 2, "name": "windows", "label": "windows"},
                ],
                comment: "",
            }
        },
        props: {
            msg: String
        },
        async created() {
            this.loading = true;
            // eslint-disable-next-line no-console
            console.log(this.$route.query.date);
            if (this.$route.query.date !== undefined) {
                this.date = new Date(this.$route.query.date).toISOString();
            }
            if (this.$route.query.category_id !== undefined) {
                this.category_id = JSON.parse(this.$route.query.category_id);
            }
            if (this.$route.query.limit !== undefined) {
                this.limit = this.$route.query.limit;
            }
            await this.refreshCategories();
            await this.refreshResources();

            // TODO: 下面这句有用吗？
            // this.$refs.waterfall.screenWidth = this.$refs.waterfall.screenWidth - 120;

            // if (this.$refs.waterfall_parent.clientWidth < 1200) {
            //     this.$refs.waterfall.width=Math.max(this.$refs.waterfall_parent.clientWidth);
            //     this.$refs.waterfall.$data["itemWidth"]=Math.max(this.$refs.waterfall_parent.clientWidth);
            // } else {
            //     this.$refs.waterfall.width=Math.max(this.$refs.waterfall_parent.clientWidth/2);
            // }
            this.loading = false;
        },
        methods: {
            loadOptions() {
                return this.categoriesOptions.children;
            },
            come_from(guid) {
                return new URL(guid).hostname
            },
            async refreshResources() {
                this.loading = true;
                this.resources = [];
                let params = {};
                let query = {};
                let category_id = [];
                if (this.category_id.length > 0) {
                    category_id = this.category_id;
                }
                if (this.category_id.length === 0 && this.date === null) {
                    this.date = new Date(Date.now());
                }
                if (this.date === null) {
                    query = params = {
                        "category_id": JSON.stringify(category_id),
                        "limit": this.limit,
                    };
                } else {
                    params = {
                        "date": this.date,
                        "category_id": JSON.stringify(category_id),
                    };
                    query = {
                        "date": new Date(this.date).toISOString(),
                        "category_id": JSON.stringify(category_id),
                    }
                }
                let resources = await api.listResources('resources', {}, params);
                let dom_parser = new DOMParser();
                for (let i = 0; i < resources.length; i++) {
                    let resource = resources[i];
                    let item = JSON.parse(resources[i].item);
                    let dom = dom_parser.parseFromString(item.description, 'text/html');
                    dom.getElementsByTagName('img').forEach(function (img) {
                        let src = img.getAttribute("src");
                        if (src.startsWith("/")) {
                            let url = new URL(resource.guid);
                            img.src = url.protocol + "//" + url.hostname + src;
                        }
                        img.referrerPolicy="no-referrer";
                    });
                    item.description = dom.body.innerHTML.substr(0, 500);
                    resource["item"] = item;
                    this.resources.push(resource);
                }
                this.$router.replace({"query": query}).catch(error => {
                    if (error.name !== "NavigationDuplicated") {
                        throw error;
                    }
                });
                this.loading = false
            },
            async refreshCategories() {
                this.loading = true;
                this.categories = await api.listResources('category');
                let root = {
                    id: 0,
                };
                this.categoriesOptions = api.generateCategoriesCursive(root, this.categories);
                this.loading = false
            },
            populateResourceToEdit(object) {
                this.intelligence_model = {
                    item: {
                        author: {
                            name: "",
                        },
                    }
                };
                this.intelligence_model.id = object.id;
                this.intelligence_model.guid = object.guid;
                this.intelligence_model.source_id = object.source_id;
                this.intelligence_model.category_id = object.category_id;
                this.intelligence_model.published_parsed = object.published_parsed;
                this.intelligence_model.item.title = object.item.title;
                this.intelligence_model.item.author.name = object.item.author.name;
                this.intelligence_model.item.description = object.item.description;
            },
            async deleteResource(id) {
                if (confirm('Are you sure you want to delete this intelligence?')) {
                    // if we are editing a intelligence we deleted, remove it from the form
                    if (this.intelligence_model.id === id) {
                        this.intelligence_model = {}
                    }
                    await api.deleteResource('resources', id);
                    await this.refreshResources()
                }
            },
            async handleOk() {
                this.loading = true;
                if (this.intelligence_model.hasOwnProperty('id') === true) {
                    // edit
                    // eslint-disable-next-line no-console
                    console.log(this.intelligence_model);
                    this.intelligence_model.item.link = this.intelligence_model.guid;
                    let data = {
                        "guid": this.intelligence_model.guid,
                        "category_id": this.intelligence_model.category_id,
                        "published_parsed": new Date().toISOString(),
                        "item": JSON.stringify(this.intelligence_model.item),
                    };
                    await api.updateResource('resources', this.intelligence_model.id, data);
                    await this.refreshResources();
                    // eslint-disable-next-line no-console
                    console.log(data);
                } else {
                    // add
                    this.intelligence_model.item.link = this.intelligence_model.guid;
                    let data = {
                        "guid": this.intelligence_model.guid,
                        "source_id": this.intelligence_model.source_id,
                        "category_id": this.intelligence_model.category_id,
                        "published_parsed": new Date().toISOString(),
                        "item": JSON.stringify(this.intelligence_model.item),
                    };
                    await api.createResource('resources', data);
                    await this.refreshResources();
                    // eslint-disable-next-line no-console
                    console.log(data);
                }
                this.loading = false;
            },
            async refreshResourcesByCategory(category_id) {
                this.date = null;
                this.category_id = [category_id];
                await this.refreshResources();
            },
            async click_comment(id, props) {
                if (this.$refs["comment-button-" + id].innerText === ' comment') {
                    this.comment = this.$refs["comment-span-" + id].innerText;
                    this.$refs["comment-span-" + id].style.display = 'none';
                    this.$refs["comment-textarea-" + id].$el.style.display = '';
                    this.$refs["comment-button-" + id].innerHTML = '<i class="fas fa-comment"> submit </i>';
                } else {
                    this.loading = true;
                    await api.submit_comment(id, this.comment);
                    this.$refs["comment-span-" + id].style.display = '';
                    props.data.comment = this.comment;
                    this.comment = '';
                    this.$refs["comment-textarea-" + id].$el.style.display = 'none';
                    this.$refs["comment-button-" + id].innerHTML = '<i class="fas fa-comment"> comment </i>';
                    this.loading = false;
                }
            },
            async click_todo(props) {
                this.loading = true;
                // eslint-disable-next-line no-console
                console.log(props.data.todo);
                let todo_type = parseInt(props.data.todo);
                this.todo = Math.abs(props.data.todo - 1);
                props.data.todo = this.todo;

                // eslint-disable-next-line no-console
                console.log(props.data.todo);
                await api.submit_todo(props.data.id, props.data.todo, todo_type);
                this.loading = false;
            }
        }
    }
</script>

