<template>
    <div>
        <div class="control-section">
            <h1 class="text-center" style="color: dodgerblue;">
                <i class="fas fa-robot" style="padding-top: 50px; padding-bottom: 50px"></i>
                捕 风
            </h1>
            <div style="width: 50%; margin: auto;">
                <b-form-input placeholder="search..." style="background: none; color: white; border: #007bff 1px solid;" type="search"></b-form-input>
            </div>
            <div class="content-wrapper" style="padding-top: 30px;">
                <!--https://ej2.syncfusion.com/vue/documentation/api/treemap/-->
                <ejs-treemap id='container' ref='treemap' align="center"
                             :if="show"
                             :key="show"
                             :drillStart='drillStart'
                             :tooltipRendering='tooltipRendering' :palette='palette'
                             :titleSettings='titleSettings'
                             format='n' :useGroupingSeparator='useGroupingSeparator'
                             :dataSource='dataSourceTest'
                             :weightValuePath='weightValuePath'
                             :tooltipSettings='tooltipSettings'
                             :leafItemSettings='leafItemSettings'
                             :levels='levels'
                             style="min-height: 700px;"
                             :selectionSettings='selectionSettings'
                             :highlightSettings='highlightSettings'
                             v-on:itemClick="greet"
                ></ejs-treemap>
                <!--                width="100%"-->
                <!--                height="100%"-->
                <!--                :load="refreshCategories"-->
                <!--                :enableDrillDown='enableDrillDown'-->
            </div>
        </div>
    </div>
</template>
<script>
    import api from '@/api';
    import Vue from 'vue';
    import {TreeMapPlugin, TreeMapTooltip} from "@syncfusion/ej2-vue-treemap";
    // import {TreeMapPlugin} from "@syncfusion/ej2-vue-treemap";

    Vue.use(TreeMapPlugin);
    // let categories = api.listResources('category');
    // let dataSourceTest1 = [api.generateDataSource({"id": 0}, categories, 0)];
    // // let dataSourceTest1 = [
    // //     {"level0": [{"Name": "test", "Population":1}]}
    // // ];
    export default Vue.extend({
        data: function () {
            return {
                // palette: ['#9999ff', '#CCFF99', '#FFFF99', '#FF9999', '#FF99FF', '#FFCC66'],
                palette: ['#C33764', '#AB3566', '#993367', '#853169', '#742F6A', '#632D6C', '#532C6D', '#412A6F', '#312870', '#1D2671'],
                titleSettings: {
                    // text: 'List of countries by population',
                    // textStyle: {size: '15px'}
                },
                enableDrillDown: true,
                useGroupingSeparator: true,
                dataSource: [],
                weightValuePath: 'Population',
                tooltipSettings: {
                    visible: true,
                    format: '${Name} : ${Population}'
                },
                show: false,
                leafItemSettings: {
                    labelPath: 'Name',
                    // labelPath: 'level1',
                    labelPosition: 'Center',
                    showLabels: true,
                    labelStyle: {size: '13px'},
                    border: {color: 'black', width: 0.5}
                },
                // https://ej2.syncfusion.com/vue/documentation/api/treemap/levelSettings/
                levels: [
                    // {groupPath: 'Continent', border: {color: 'white', width: 0.5}},
                    // {groupPath: 'States', border: {color: 'white', width: 0.5}},
                    // {groupPath: 'Region', showHeader: true, border: {color: 'white', width: 0.5}},
                    {groupPath: 'level0', showHeader: true, border: {color: 'white', width: 0.5}, headerAlignment: 'Center', headerStyle: { size: '16px' },},
                    {groupPath: 'level1', showHeader: true, border: {color: 'white', width: 0.5}, headerAlignment: 'Center', headerStyle: { size: '16px' },},
                    {groupPath: 'level2', showHeader: true, border: {color: 'white', width: 0.5}, headerAlignment: 'Center', headerStyle: { size: '16px' },},
                ],
                headerdata: [
                    {value: 'Near', text: 'Near'},
                    {value: 'Far', text: 'Far'},
                    {value: 'Center', text: 'Center'}
                ],
                localFields: {text: 'value', value: 'text'},
                labelswidth: 120,
                dataSourceTest: [],
                selectionSettings: {
                    enable: true,
                    fill: '#58a0d3',
                    border: { width: 0.3, color: 'black' },
                    opacity: '1'
                },
                highlightSettings: {
                    enable: true,
                    fill: '#71b0dd',
                    border: { width: 0.3, color: 'black' },
                    opacity: '1'
                },
            }
        },
        provide: {
            treemap: [TreeMapTooltip]
        },
        // async beforeCreate() {
        //     let root = {
        //         "id": 0,
        //     };
        //     let categories = await api.listResources('category');
        //     // for(let category in categories) {
        //     //     this.dataSource.push({cccc: })
        //     // }
        //
        //     Vue.set(this.dataSourceTest, [this.generateDataSource(root, categories, 0)]);
        // },
        async created() {
            this.loading = true;
            await this.refreshCategories();
            this.loading = false
        },
        // computed: {
        //     dataSourceTest2: function () {
        //         return this.dataSourceTest
        //     }
        // },
        methods: {
            async refreshCategories() {
                this.loading = true;
                this.show = false;
                let root = {
                    "id": 0,
                };
                let categories = await api.listResources('category');
                // for(let category in categories) {
                //     this.dataSource.push({cccc: })
                // }

                this.dataSourceTest = [this.generateDataSource(root, categories, 0)];
                // eslint-disable-next-line no-console
                this.show = true;
                this.loading = false
            },
            generateDataSource(parent, categories, level) {
                let group_name = "level" + level;
                parent[group_name] = [];
                for (let i = 0; i < categories.length; i++) {
                    let category = categories[i];
                    category.parent = parseInt(category.parent);
                    parent.id = parseInt(parent.id);
                    if (category.parent === parent.id) {
                        category.Name = category.name;
                        category.Population = 1;
                        parent[group_name].push(category);
                        // parent[group_name].push({
                        //     "Name": category.name,
                        //     "Population": 1,
                        // });
                    }
                }
                for (let i = 0; i < parent[group_name].length; i++) {
                    let child = parent[group_name][i];
                    child = this.generateDataSource(child, categories, level + 1);
                    parent[group_name][i] = child;
                }
                parent.Population = parent[group_name].length+1;
                return parent
            },
            drillStart: function (args) {
                if (args.item[Object.keys(args.item)[0]].length === 1) {
                    args.treemap.levels[2].showHeader = true;
                } else {
                    args.treemap.levels[2].showHeader = false;
                }
            },
            tooltipRendering: function (args) {
                if (args.item['groupIndex'] !== 2) {
                    args.cancel = true;
                }
            },
            changeHeader: function (args) {
                let treemap = this.$refs.treemap.ej2Instances;
                for (let i = 0; i < treemap.levels.length - 1; i++) {
                    treemap.levels[i].headerAlignment = args.value;
                }
                treemap.refresh();
            },
            changeLabels: function (args) {
                let treemap = this.$refs.treemap.ej2Instances;
                treemap.levels[2].headerAlignment = args.value;
                treemap.refresh();
            },
            changeDrillView: function (args) {
                let treemap = this.$refs.treemap.ej2Instances;
                if (args.checked === true) {
                    treemap.drillDownView = true;
                } else {
                    treemap.drillDownView = false;
                }
                treemap.refresh();
            },
            changebreadCrumb: function () {
                let treemap = this.$refs.treemap.ej2Instances;
                this.$refs.treemap.ej2Instances.enableBreadcrumb = this.$refs.breadCrumb.ej2Instances.checked;
                let breadCrumbText = document.getElementById('breadCrumbText').value;
                treemap.breadCrumb = breadCrumbText;
                treemap.refresh();
            },
            changebreadCrumbText: function () {
                // let treemap = this.$refs.treemap.ej2Instances;
            },
            greet: function (event) {
                // eslint-disable-next-line no-console
                console.log(event.item.id);
                this.$router.push({ name: 'Intelligence', query: {
                    category_id: JSON.stringify([event.item.id])
                }});
            }
        }
    })
</script>
<style>
    .content-wrapper rect {
        fill: transparent !important;
    }

    .content-wrapper g text {
        fill: white !important;
    }

    path {
        opacity: 0.3;
        /*fill: rgb(60, 119, 191);*/
        fill: rgb(128, 128, 128);
        stroke: white;
        stroke-width: 0.5;
    }
</style>