<template>
    <div>
        <b-alert :show="loading" variant="info">Loading...</b-alert>
        <b-row class="no-margin-right padding-left">
            <b-col>
                <table class="table table-striped table-resources-list">
                    <thead>
                    <tr>
                        <th>Id</th>
                        <th>Name</th>
                        <th>Parent</th>
                        <th>action</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="category in resources" :key="category.id">
                        <td>{{ category.id }}</td>
                        <td>{{ category.name }}</td>
                        <td>{{ category.parent }}</td>
                        <td>
                            <a href="#" class="fa fa-pen" @click.prevent="populateResourceToEdit(category)"></a>
                            <a href="#" class="fa fa-times" style="padding-left: 15px"
                               @click.prevent="deleteResource(category.id)"></a>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </b-col>
            <b-col lg="4">
                <div class="add_resource">
                    <b-card :title="(model.id ? 'Edit Category ID#' + model.id : 'New Category')">
                        <b-input-group prepend="name">
                            <b-input v-model="model.name"></b-input>
                        </b-input-group>
                        <br>
                        <treeselect placeholder="select parent" v-model="model.parent" :multiple="true"
                                    :options="categoriesOptions.children"/>
                        <br>
                        <p v-if="model.hasOwnProperty('parent')">selected:
                            {{typeof(model.parent)==='string'?model.parent:model.parent[0]}}</p>
                        <b-btn-group>
                            <b-btn variant="outline-primary" @click="saveResource">
                                {{model.id? 'Save' : 'Add'}}
                            </b-btn>
                            <b-btn variant="outline-primary" @click="model = {}">Reset</b-btn>
                        </b-btn-group>
                    </b-card>
                </div>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import api from "@/api";
    // import the component
    import Treeselect from '@riophae/vue-treeselect'
    // import the styles
    import '@riophae/vue-treeselect/dist/vue-treeselect.css'

    export default {
        name: "Category",
        // register the component
        components: {Treeselect},
        data() {
            return {
                model: {},
                loading: false,
                url: '',
                resources: [],
                categories: [{'value': 1, 'text': 'ca'}],
                categoriesOptions: []
            }
        },
        async created() {
            this.loading = true;
            await this.refreshResources();
            await this.refreshCategories();
            this.loading = false
        },
        methods: {
            async refreshResources() {
                this.loading = true;
                this.resources = await api.listResources('category');
                this.loading = false
            },
            async refreshCategories() {
                this.loading = true;
                let root = {
                    id: 0,
                };
                this.categoriesOptions = api.generateCategoriesCursive(root, this.resources);
                // eslint-disable-next-line no-console
                console.log(this.categoriesOptions.children);
                this.loading = false
            },
            async saveResource() {
                this.loading = true;
                if (this.model.hasOwnProperty('parent') === true) {
                    this.model.parent = parseInt(this.model.parent[0]);
                } else {
                    this.model.parent = 0
                }
                if (this.model.id) {
                    await api.updateResource('category', this.model.id, this.model)
                } else {
                    await api.createResource('category', this.model);
                }
                this.model = {};
                await this.refreshResources();
                await this.refreshCategories();
                this.loading = false;
            },
            populateResourceToEdit(object) {
                this.model = Object.assign({}, object)
            },
            async deleteResource(id) {
                if (confirm('Are you sure you want to delete this category?')) {
                    // if we are editing a category we deleted, remove it from the form
                    if (this.model.id === id) {
                        this.model = {}
                    }
                    await api.deleteResource('category', id);
                    await this.refreshResources()
                }
            },
        }
    }
</script>

