<template>
    <div>
        <b-alert :show="loading" variant="info">Loading...</b-alert>
        <div style="padding-left: 20px; padding-bottom: 10px;">
            <span>last time fetch: {{new Date(last_update).toLocaleString()}}</span>
            <b-btn style="margin-left: 10px;" variant="info" @click="refresh()">
                <i class="fas fa-redo-alt"></i> refresh
            </b-btn>
        </div>
        <b-row class="no-margin-right padding-left">
            <b-col>
                <table class="table table-striped table-resources-list">
                    <thead>
                    <tr>
                        <th>Id</th>
                        <th>FeedURL</th>
                        <th>Author</th>
                        <th>Category</th>
                        <th>Status</th>
                        <th>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="source in sources" :key="source.id">
                        <td>{{ source.id }}</td>
                        <td>{{ source.feed_url }}</td>
                        <td>{{ source.author }}</td>
                        <td>{{ source.category_id }}</td>
                        <td>
                            <i v-if="source.fetch_status==='1'" class="fas fa-smile-wink"
                               style="color: greenyellow"></i>
                            <i v-else class="far fa-frown" style="color: red"></i>
                        </td>
                        <td>
                            <a href="#" class="fa fa-pen" @click.prevent="populateResourceToEdit(source)"></a>
                            <a href="#" class="fa fa-times" style="padding-left: 15px"
                               @click.prevent="deleteResource(source.id)"></a>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </b-col>
            <b-col lg="4">
                <div class="add_resource">
                    <b-input-group prepend="source">
                        <b-input v-model="model.feed_url"></b-input>
                        <b-button variant="outline-primary" @click="parse">parse</b-button>
                    </b-input-group>
                    <br>
                    <img v-if="parse_result.toString().length>0 && parse_result.hasOwnProperty('image')"
                         :src="parse_result.image.url" alt="">
                    <p v-if="parse_result.toString().length>0">
                        [title] <a :href="parse_result.link" target="_blank">{{parse_result.title}}</a>
                        &emsp;
                        <i class="fas fa-pen" v-if="parse_result.hasOwnProperty('author')">[author]
                            {{parse_result.author.name}}</i>
                        &emsp;
                        <i class="fas fa-envelope" v-if="parse_result.hasOwnProperty('author')">[email]
                            {{parse_result.author.email}}</i>
                    </p>
                    <p v-if="parse_result.toString().length>0">[updatedParsed] {{parse_result.updatedParsed}}
                        [generator] {{parse_result.generator}}
                        [feedType] {{parse_result.feedType}} [feedVersion] v{{parse_result.feedVersion}}</p>
                    <p v-if="parse_result.toString().length>0">{{parse_result.description}}</p>
                    <!--<pre> {{parse_result}} </pre>-->

                    <!--                    <b-input-group prepend="category">-->
                    <!--                        <b-select :options="categories" v-model="model.category">-->
                    <!--                            <optgroup label="g">-->
                    <!--                                <option>opt</option>-->
                    <!--                            </optgroup>-->
                    <!--                        </b-select>-->
                    <!--                    </b-input-group>-->
                    <treeselect placeholder="select category" v-model="model.category_id" :multiple="true"
                                :options="categoriesOptions.children"/>
                    <br>
                    <div v-if="model.id">
                        <b-input-group prepend="Author">
                            <b-input v-model="model.author"></b-input>
                        </b-input-group>
                        <br>
                    </div>
                    <b-btn-group>
                        <b-button variant="outline-primary" @click="saveResource">{{model.id? 'Save' : 'Add'}}
                        </b-button>
                        <b-btn variant="outline-primary" @click="model = {}">Reset</b-btn>
                    </b-btn-group>
                </div>
            </b-col>
        </b-row>
    </div>
</template>

<script>
    import api from "@/api";
    // import the component
    import Treeselect from '@riophae/vue-treeselect'
    // import the styles
    import '@riophae/vue-treeselect/dist/vue-treeselect.css'

    export default {
        name: "Source",
        // register the component
        components: {Treeselect},
        data() {
            return {
                model: {},
                parse_result: '',
                loading: false,
                url: '',
                sources: [],
                categories: ["ca"],
                categoriesOptions: [],
                last_update: 'have not',
            }
        },
        async created() {
            this.loading = true;
            await this.refreshResources();
            await this.refreshCategories();
            await this.refreshLastUpdate();
            this.loading = false
        },
        methods: {
            async refreshResources() {
                this.loading = true;
                this.sources = await api.listResources('sources');
                this.loading = false
            },
            async refreshCategories() {
                this.loading = true;
                let categories = await api.listResources('category');
                let root = {
                    id: 0,
                };
                this.categoriesOptions = api.generateCategoriesCursive(root, categories);
                // eslint-disable-next-line no-console
                console.log(this.categoriesOptions.children);
                this.loading = false
            },
            async refreshLastUpdate() {
                this.loading = true;
                let response = await api.refreshLastUpdate();
                this.last_update = response.last_update;
                this.loading = false;
            },
            parse: async function () {
                this.loading = true;
                await api.parse_source(this.model.feed_url).then(
                    response => {
                        this.parse_result = response;
                    }
                );
                this.loading = false;
            },
            async saveResource() {
                this.loading = true;
                this.model.parent = parseInt(this.model.id);
                this.model.category_id = this.model.category_id[0];
                if (this.model.id) {
                    this.model.fetch_status = this.model.fetch_status === '1';
                    await api.updateResource('sources', this.model.id, this.model)
                } else {
                    await api.createResource('sources', this.model);
                }
                this.model = {};
                await this.refreshResources();
                this.loading = false;
            },
            populateResourceToEdit(object) {
                this.model = Object.assign({}, object)
            },
            async deleteResource(id) {
                if (confirm('Are you sure you want to delete this source?')) {
                    // if we are editing a category we deleted, remove it from the form
                    if (this.model.id === id) {
                        this.model = {}
                    }
                    await api.deleteResource('source', id);
                    await this.refreshResources()
                }
            },
            async refresh() {
                this.loading = true;
                await api.refresh();
                this.loading = false;
            }
        }
    }
</script>

