        <b-alert :show="error.length>0" variant="warning">{{error}}</b-alert>
        <div class="date_select row">
            <div class="col"></div>
            <div class="col-1">
                <b-btn @click="refreshResources">
                    <i class="fas fa-redo-alt"></i>
                </b-btn>
            </div>
            <div class="col-1">
                <b-btn v-b-modal.modal-1 style="" @click="delete todo_model['id']">
                    <i class="fa fa-plus"></i>
                </b-btn>
            </div>
        </div>
        <b-modal id="modal-1"
                 :header-bg-variant="headerBgVariant"
                 :header-text-variant="headerTextVariant"
                 :body-bg-variant="bodyBgVariant"
                 :body-text-variant="bodyTextVariant"
                 :footer-bg-variant="footerBgVariant"
                 :footer-text-variant="footerTextVariant"
                 @ok="handleOk"
        >
            <template v-slot:modal-title>
                {{todo_model.id ? 'Edit Todo ID#' + todo_model.id : 'New Todo'}}
            </template>
            <b-form-group class="add_intelligence">
                <b-input-group prepend="name">
                    <b-form-input v-model="todo_model.name"></b-form-input>
                </b-input-group>
                <br>
                <b-form-textarea v-model="todo_model.content" rows="3" max-rows="8"></b-form-textarea>
            </b-form-group>
        </b-modal>
        <div ref="waterfall_parent" style="padding-bottom: 20px">
            <Waterfall
                    :list="resources"
                    :gutter="10"
                    :width="540"
                    :phoneCol="1"
                    backgroundColor="rgb(73, 74, 95, 0.3)"
                    ref="waterfall"
            >
                <template slot="item" slot-scope="props">
                    <b-card class="intelligence_card" v-if="parseInt(props.data.todo_type, 10)===0">
                        <b-card-header>
                            <p>
                                <span v-if="props.data.item.hasOwnProperty('author')" class="fas fa-feather-alt"
                                      style="padding-right: 10px;"> {{props.data.item.author.name}} </span>
                                <a target="_blank" :href="props.data.item.link" class="fas fa-external-link-alt">
                                    {{props.data.item.title}} </a>
                            </p>
                        </b-card-header>
                        <b-card-body>
                            <i class="fas fa-align-left" style="padding-right: 10px;"></i>
                            <span class="description-span" v-html="props.data.item.description"></span>
                        </b-card-body>
                        <b-card-footer>
                            <span class="far fa-clock" style="padding-right: 10px;"> {{new Date(props.data.published_parsed.replace(" ", "T")+"Z").toLocaleString()}} </span>
                            <a :href="'/#/intelligence?category_id=[' + props.data.category_id + ']'">
                                <span>
                                    <i class="fas fa-coins"> {{props.data.category_name}} </i>
                                </span>
                            </a>
                            <i class="fab fa-chrome" style="padding-left:10px; padding-right:5px;"></i><span>{{come_from(props.data.guid)}}</span>
                            <br>
                            <i class="fas fa-hashtag"></i>
                            <span>{{props.data.item.tags}}</span>
                            <a href="#" style="margin-left: 3px;"> #tag1 </a>
                            <a href="#" style="margin-left: 3px;"> #tag2 </a>
                            <br>
                            <div>
                                <hr style="border-top: 1px solid rgba(255, 255, 255, 0.4)"
                                    v-show="props.data.comment.length>0">
                                <span style="font-size: 30px;" v-show="props.data.comment.length > 0">“</span>
                                <span :ref="'comment-span-'+props.data.id"> {{props.data.comment }} </span>
                                <b-textarea :ref="'comment-textarea-'+props.data.id" v-model="comment"
                                            v-show="false">
                                </b-textarea>
                                <span style="font-size: 30px;" v-show="props.data.comment.length>0">”</span>
                            </div>
                            <span style="float: right">
                                <b-btn @click="click_todo(props)">
                                    <i class="fas fa-clipboard-list"
