                                </b-btn>
                                <b-btn :ref="'comment-button-'+props.data.id" @click="click_comment(props.data.id)">
                                    <i class="fas fa-comment"> comment </i>
                                </b-btn>
                                <b-btn v-b-modal.modal-1 style="margin-left: 10px;"
                                       @click="populateResourceToEdit(props.data)">
                                    <i class="fas fa-pen"> edit </i>
                                </b-btn>
                                <b-btn style="margin-left: 10px;" @click="deleteResource(props.data.id)">
                                    <i class="fas fa-times"> delete </i>
                                </b-btn>
                            </span>
                        </b-card-footer>
                    </b-card>
